    toFullScreen() {
      this.col = false;
      let time = setInterval(() => {
        if (
          document.getElementsByTagName("body")[0].offsetWidth >
          document.getElementsByTagName("body")[0].offsetHeight
        ) {
          this.showBBB = false;
          this.showSide(2);
          clearInterval(time);
        }
      }, 10);
      plus.screen.lockOrientation("landscape-primary");
      this.showBBB = true;
      let W = plus.navigator.getStatusbarHeight();
      document.getElementsByClassName("kl-content")[0].style.paddingLeft =
        W + "px";
      this.refreshenB = false;
      plus.navigator.setFullscreen(true);
      this.newtime = 0;
    },
    selectFive(num) {
      if (num === 1) {
        this.showFive = true;
      } else if (num == 2) {
        this.showFive = false;
      }
    },
    toLandscape() {
      if (!this.col) {
        return;
      }
      let newtime = new Date();
      if (this.newtime == 0) {
        this.newtime = new Date();
        setTimeout(() => {
          this.newtime = 0;
        }, 310);
      } else {
        if (newtime - this.newtime < 300) {
          setTimeout(() => {
            this.col = false;
            let time = setInterval(() => {
              if (
                document.getElementsByTagName("body")[0].offsetWidth >
                document.getElementsByTagName("body")[0].offsetHeight
              ) {
                this.showBBB = false;
                this.showSide(2);
                clearInterval(time);
              }
            }, 10);
          }, 100);
          plus.screen.lockOrientation("landscape");
          this.showBBB = true;
          let W = plus.navigator.getStatusbarHeight();
          document.getElementsByClassName("kl-content")[0].style.paddingLeft =
            W + "px";
          this.refreshenB = false;
          plus.navigator.setFullscreen(true);
          this.newtime = 0;
        } else {
          return;
        }
      }
    },
    back() {
      plus.screen.lockOrientation("portrait-primary"); //竖屏
      this.showBBB = true;
