      this.handdleMsgq();
      // },100)
    },
    handdleMsgq() {
      //获取初始持仓单
      let this_ = this;
      if (this_.global.ws && this_.global.ws.readyState === 1) {
        this_.global.ws.onopen = this_.onopenq();
      }
      this_.global.ws.onmessage = function (res) {
        console.log(res);
        this_.websocketonmessageq(res);
      };
    },
    onopenq() {
      // websocket连接后发送数据(send发送)
      let actions, balance;
      let this_ = this;
      actions = {
        action: "loadposition",
        reqID: 45,
        data: {
          userId: this_.uid,
        },
      };
      this_.global.ws.send(JSON.stringify(actions));
      console.log(JSON.stringify(actions));
    },
    onopen2() {
      // websocket连接后发送数据(send发送)
      let balance;
      let this_ = this;
      balance = {
        action: "loadusermoney",
        reqID: 18,
      };
      this_.global.ws.send(JSON.stringify(balance));
    },
    websocketonmessageq(e) {
      // 数据接收
      this.redata = JSON.parse(e.data);
      console.log(this.redata);
      let _this = this;
      if (this.redata.data.code === 0 && this.redata.data.object != null) {
        this.MergeState = false;
        this.holdPositions = this.redata.data.object;
        let buyPriceJ = 0; //开仓买价和
        let buycount = 0;
        let sellPriceJ = 0;
        let sellcount = 0;
        for (let i = 0; i < this.holdPositions.length; i++) {
          if (this.holdPositions[i].direction === 0) {
            this.holdPositions[i].direction = "买";
          } else if (this.holdPositions[i].direction === 1) {
            this.holdPositions[i].direction = "卖";
          }
          let thisTowDate = new Date(this.holdPositions[i].opentime);
          let currentTowTime =
            thisTowDate.getFullYear() +
            "-" +
            (thisTowDate.getMonth() + 1) +
            "-" +
            thisTowDate.getDate() +
            " " +
            thisTowDate.getHours() +
            ":" +
            thisTowDate.getMinutes();
          this.holdPositions[i].opentime = currentTowTime;
        }
        console.log(this.sellBuy);
        // let changeArr = this.mergeArr(this.holdPositions)
        let result = this.holdPositions.reduce((groups, item) => {
          let groupFound = groups.find(
            (arrItem) =>
              item.instrumentid === arrItem.instrumentid &&
              item.direction === arrItem.direction
          );
          // console.log((item.openPrice*item.volume).toFixed(2))
          item.openPrice = (
            item.openPrice *
            (item.volume - item.volumeClosed - item.volumeFrozen)
          ).toFixed(2);
          if (groupFound) {
            groupFound.volume +=
              item.volume - item.volumeClosed - item.volumeFrozen;
            groupFound.openPrice =
              Number(groupFound.openPrice) + Number(item.openPrice);
            groupFound.margin += item.margin;
            groupFound.commission += item.commission;
            if (groupFound.orderid.indexOf(item.orderid) == -1) {
              //去重
              groupFound.orderid.push(item.orderid);
            }
            if (groupFound.id.indexOf(item.id) == -1) {
              //去重
              groupFound.id.push(item.id);
            }
            if (groupFound.opentime.indexOf(item.opentime) == -1) {
