    // 获取合约乘数
    getMultiplier(result) {
      console.log(this.discUrl);
      this.$axios
        .get("http://" + this.discUrl + ":8189/instruments")
        .then((res) => {
          // this.$axios.get('http://47.110.226.247:8189/instruments').then(res =>{
          let instrumentNameList = res.data.data;
          console.log(instrumentNameList);
          for (let i = 0; i < instrumentNameList.length; i++) {
            for (let j = 0; j < result.length; j++) {
              if (
                instrumentNameList[i].instrumentid === result[j].instrumentid
              ) {
                result[j].priceperpoint = instrumentNameList[i].priceperpoint;
                result[j].name = instrumentNameList[i].nameCh;
                result[j].currencyQuote = instrumentNameList[i].currencyQuote;
                result[j].groupid = instrumentNameList[i].groupid; //代号‘cl'
                result[j].commissionClose =
                  instrumentNameList[i].commissionClose; //平仓手续费
              }
            }
          }
          this.exchangeRate(result);
        });
    },
    // 获取汇率
    exchangeRate(result) {
      let _this = this;
      this.$axios
        .get("http://" + this.discUrl + ":7777/api/exchangerate/all")
        .then((res) => {
          if (res.data.code === 0) {
            _this.rateList = res.data.object;
            console.log(res.data.object);
            for (let i = 0; i < result.length; i++) {
              result[i].volume =
                result[i].volume -
                result[i].volumeClosed -
                result[i].volumeFrozen;
              result[i].average = (
                result[i].openPrice / result[i].volume
              ).toFixed(2);
              result[i].showContent = false;
              result[i].NEWPRICE = result[i].average;
              result[i].NEWPL = 0;
              result[i].NEWCPL = 0;
              for (let j = 0; j < _this.rateList.length; j++) {
                if (_this.rateList[j].currency === result[i].currencyQuote) {
                  result[i].rate = _this.rateList[j].rate;
                }
              }
            }
            _this.holdPositions = result;
            this.MergeState = true;
            for (let i = 0; i < result.length; i++) {
              if (result[i].instrumentid === this.instrumentId) {
                if (result[i].direction === "买") {
                  this.sellBuy.buyPriceJ = (
                    result[i].openPrice / result[i].volume
                  ).toFixed(this.digits);
                  this.sellBuy.buyVol = result[i].volume;
                } else if (result[i].direction === "卖") {
                  this.sellBuy.sellPriceJ = (
                    result[i].openPrice / result[i].volume
                  ).toFixed(this.digits);
                  this.sellBuy.sellVol = result[i].volume;
                }
              }
            }
            this.tickCopy = JSON.parse(localStorage.getItem("tickCopy"));
            for (let tck in this.tickCopy) {
              if (tck === this.instrumentId) {
                this.tick = this.tickCopy[tck];
                this.tickname2 = this.tick["instrumentid"];
                this.newPrice = this.tick["price"];
                this.open = this.tick.open;
                this.high = this.tick.high;
                this.low = this.tick.low;
                this.preclose = this.tick.preclose;
                this.positionvol = this.tick.positionvol;
                this.totalvol = this.tick.totalvol;
                //计算浮动盈亏
                for (let i = 0; i < this.holdPositions.length; i++) {
                  if (
                    this.holdPositions[i].instrumentid === this.instrumentId
                  ) {
                    console.log(this.holdPositions[i].rate);
                    if (
                      this.holdPositions[i].direction === "卖" &&
                      this.holdPositions[i].rate !== undefined
                    ) {
                      this.sellBuy.sellMoney =
                        (
                          (Number(this.tick["price"]) -
                            this.sellBuy.sellPriceJ) *
                          this.holdPositions[i].priceperpoint *
                          this.sellBuy.sellVol *
                          this.holdPositions[i].rate
                        ).toFixed(2) * -1;
                      console.log(this.holdPositions[i].rate);
                    } else if (
                      this.holdPositions[i].direction === "买" &&
                      this.holdPositions[i].rate !== undefined
                    ) {
                      this.sellBuy.buyMoney = (
                        (Number(this.tick["price"]) - this.sellBuy.buyPriceJ) *
                        this.holdPositions[i].priceperpoint *
                        this.sellBuy.buyVol *
                        this.holdPositions[i].rate
                      ).toFixed(2);
                    }
                    this.sellBuy.priceperpoint = this.holdPositions[
                      i
                    ].priceperpoint;
                    this.sellBuy.rate = this.holdPositions[i].rate;
                  }
                }
                break;
              }
            }
          }
        });
