    setTick() {
      for (let tck in this.tickCopy) {
        if (tck === this.$route.params.instrumentId) {
          this.tick = this.tickCopy[tck];
          this.tick["price"] = Number(this.tick["price"]).toFixed(this.digits);
          this.tick["bidprice"] = Number(this.tick["bidprice"]).toFixed(
            this.digits
          );
          this.tick["bidprice2"] = Number(this.tick["bidprice2"]).toFixed(
            this.digits
          );
          this.tick["bidprice3"] = Number(this.tick["bidprice3"]).toFixed(
            this.digits
          );
          this.tick["bidprice4"] = Number(this.tick["bidprice4"]).toFixed(
            this.digits
          );
          this.tick["bidprice5"] = Number(this.tick["bidprice5"]).toFixed(
            this.digits
          );
          this.tick["askprice"] = Number(this.tick["askprice"]).toFixed(
            this.digits
          );
          this.tick["askprice2"] = Number(this.tick["askprice2"]).toFixed(
            this.digits
          );
          this.tick["askprice3"] = Number(this.tick["askprice3"]).toFixed(
            this.digits
          );
          this.tick["askprice4"] = Number(this.tick["askprice4"]).toFixed(
            this.digits
          );
          this.tick["askprice5"] = Number(this.tick["askprice5"]).toFixed(
            this.digits
          );
          this.tickname2 = this.tick["instrumentid"];
          if (this.tick["preclose"] == 0) {
            this.diffPrice = 0;
            this.diffPriceRate = "-%";
          } else {
            this.diffPrice = (
              this.tick["price"] - this.tick["preclose"]
            ).toFixed(this.digits);
            if (
              this.tick["price"] - this.tick["preclose"] < 0.01 &&
              this.tick["price"] - this.tick["preclose"] !== 0
            ) {
              this.diffPriceRate = "0.01%";
            } else {
              this.diffPriceRate =
                (
                  Math.abs(this.diffPrice / this.tick["preclose"]) * 100
                ).toFixed(2) + "%";
            }
          }
          break;
        }
      }
