      let num = event.touches[0].screenY - this.Y;
      if (Math.abs(num) > 100) {
        return;
      }
      // document.getElementsByClassName('dt-con-k')[0].style.transform = "translateY("+ num +"px)";
      document.getElementsByClassName("charts")[0].style.transform =
        "translateY(" + num + "px)";
      this.isLoading = num;
    },
    downEnd() {
      if (this.col) {
        return;
      }
      // document.getElementsByClassName('dt-con-k')[0].style.transform = "translateY(0px)";
      document.getElementsByClassName("charts")[0].style.transform =
        "translateY(0px)";
      if (Math.abs(this.isLoading) < 30) {
        return;
      }
      if (this.isLoading > 0) {
        this.onRefresh();
        this.isLoading = 0;
      } else if (this.isLoading < 0) {
        this.onRefreshPre();
        this.isLoading = 0;
      }
    },
