              if (this.holdPositions[i].instrumentid === this.instrumentId) {
                if (
                  this.holdPositions[i].direction === "卖" &&
                  this.holdPositions[i].rate !== undefined
                ) {
                  this.sellBuy.sellMoney =
                    (
                      (Number(this.tick["price"]) - this.sellBuy.sellPriceJ) *
                      this.holdPositions[i].priceperpoint *
                      this.sellBuy.sellVol *
                      this.holdPositions[i].rate
                    ).toFixed(2) * -1;
                } else if (
                  this.holdPositions[i].direction === "买" &&
                  this.holdPositions[i].rate !== undefined
                ) {
                  this.sellBuy.buyMoney = (
                    (Number(this.tick["price"]) - this.sellBuy.buyPriceJ) *
                    this.holdPositions[i].priceperpoint *
                    this.sellBuy.buyVol *
                    this.holdPositions[i].rate
                  ).toFixed(2);
                }
                this.sellBuy.priceperpoint = this.holdPositions[
                  i
                ].priceperpoint;
                this.sellBuy.rate = this.holdPositions[i].rate;
              }
            }
            //计算当前选择数量的浮动盈亏
            // if(this.text==='买'){
            //   this.money=(this.sellBuy.priceperpoint*this.sellBuy.rate*this.value*(Number(this.tick['price'])-this.sellBuy.buyPriceJ)).toFixed(2);
            //   this.lastList.money=this.money;
            // }else if(this.text==='卖') {
            //   this.money=(this.sellBuy.priceperpoint*this.sellBuy.rate*this.value*(Number(this.tick['price'])-this.sellBuy.sellPriceJ)).toFixed(2);
            //   this.lastList.money=this.money;
            // }
            break;
          }
        }
        for (let i = 0; i < newVal.length; i++) {
          if (newVal[i].instrumentid == this.instrumentId) {
            let newTime = new Date(Number(newVal[i]["time"]));
            let h = newTime.getHours();
            let m = newTime.getMinutes();
            let s = newTime.getSeconds();
            if (h < 10) {
              h = "0" + h;
            }
            if (m < 10) {
              m = "0" + m;
            }
            if (s < 10) {
              s = "0" + s;
            }
            let mixTime = h + ":" + m + ":" + s;
            let obj = {
              time: mixTime,
              price: newVal[i]["price"].toFixed(this.digits),
              vol: newVal[i]["tickvol"],
            };
            if (this.tickList2.length < 13) {
              this.tickList2.push(obj);
            } else {
              this.tickList2.shift();
              this.tickList2.push(obj);
            }
            if (this.tickList.length < this.listNum) {
              this.tickList.push(obj);
            } else {
              this.tickList.shift();
              this.tickList.push(obj);
            }
          }
        }
      },
      deep: true,
    },
    changeSave() {
      this.reload();
    },
  },
};
</script>
