          transition: all 0.7s;
          overflow: auto;
          &.hideC {
            top: 40px;
            left: tovhw(-300);
          }
          &.openC {
            top: 40px;
            left: tovhw(0);
          }
        }
        & > span {
          position: absolute;
          font-family: "iconfont" !important;
          right: tovh(19);
          top: tovh(14);
          font-size: tovh(16);
        }
      }
      .kl-content {
        width: 100%;
        height: 325px;
        display: flex;
        position: relative;
        .kl-content-L {
          /*transition: all 0.4s;*/
          /*flex: 1;*/
          width: 794px;
          background-color: #fff;
          position: relative;
          @media screen and (orientation: landscape) {
            .sideTick {
              position: absolute;
              top: tovh(0);
              width: 145px;
              background-color: #fff;
              height: 325px;
              border-left: tovh(1) solid #ebebeb;
              transition: all 0.4s;
              .title {
                display: flex;
                font-size: 0;
                height: 33px;
                & > span {
                  display: inline-block;
                  font-size: tovh(12);
                  text-align: center;
                  line-height: 33px;
                  flex: 1;
                  & > span {
                    display: inline-block;
                    width: 48px;
                    height: 17px;
                    border-radius: tovh(8);
                    line-height: 17px;
                    color: #666666;
                    &.changeC {
                      background-color: #1988ff;
                      color: #fff;
                    }
                  }
                }
              }
              .sellBuy {
                height: 292px;
                .st-buy {
                  border-bottom: tovh(1) solid #ebebeb;
                }
                .st-buy,
                .st-sell {
                  width: 100%;
                  height: 50%;
                  display: flex;
                  min-width: tovh(110);
                  flex-direction: column;
                  padding: 6px tovh(5);
                  .st-b {
                    flex: 1;
                    display: flex;
                  }
                  .st-s {
                    flex: 1;
                    display: flex;
                  }
                  .st-type {
                    display: inline-block;
                    text-align: center;
                    width: 33px;
                    font-size: tovh(12);
                    line-height: 26px;
                    &.Red {
                      color: #fd4f50;
                    }
                    &.Gre {
                      color: #07b76f;
                    }
                  }
                  .st-price {
                    display: inline-block;
                    text-align: center;
                    font-size: tovh(10);
                    flex: 1;
                    line-height: 27.5px;
                    &.Red {
                      color: #fd4f50;
                    }
                    &.Gre {
                      color: #07b76f;
                    }
                  }
                  .st-vol {
                    flex: 0.7;
                    display: inline-block;
                    text-align: center;
                    font-size: tovh(12);
                    line-height: 27px;
                  }
                }
              }
              .tickDetail {
                width: 100%;
                height: tovh(280);
              }
              &.open {
                right: tovh(0);
              }
              &.hide {
                right: -160px;
              }
              .open-side {
                z-index: 4;
                font-family: "iconfont" !important;
                font-size: tovh(18);
                line-height: tovh(30);
                border-radius: tovh(10) 0 0 tovh(10);
                position: absolute;
                top: 50%;
                /*right: tovhw(120);*/
                left: tovhw(-17);
                display: block;
                width: tovhw(16);
                height: tovh(30);
                margin-top: tovh(-15);
                /*background-color: #FFF;*/
                font-weight: 700;
                border-right: 0;
                color: #222222;
              }
              .list {
                display: flex;
                height: 23px;
                padding: tovh(6) tovh(5) tovh(0) tovh(5);
                & > span:nth-child(1) {
                  font-size: tovh(12);
                  width: 55px;
                  text-align: center;
                  line-height: 23px;
                  color: #222222;
                }
                & > span:nth-child(2) {
                  font-size: tovh(10);
                  text-align: center;
                  line-height: 25px;
                  flex: 1;
                  &.changeRed {
                    color: #fd4f50;
                  }
                  &.changeGre {
                    color: #07b76f;
                  }
                }
                & > span:nth-child(3) {
                  flex: 0.7;
                  font-size: tovh(12);
                  text-align: center;
                  line-height: 23px;
                  &.changeRed {
                    color: #fd4f50;
                  }
                  &.changeGre {
                    color: #07b76f;
                  }
                }
              }
            }
          }
        }
        .kl-content-R {
          padding-bottom: 30px;
          width: 54px;
          background-color: #fff;
          border-left: 1px solid #ebebeb;
          position: relative;
          height: 294px;
          overflow: scroll;
          & > span {
            display: block;
            text-align: center;
            font-size: 13px;
            height: 25px;
            line-height: 25px;
            &.changeC {
              color: #ff7100;
            }
            &.disable {
              color: #c4c3c3;
            }
          }
        }
        .kl-content-R::-webkit-scrollbar {
          /*display: none;*/
          width: 1px;
          background-color: rgba(225, 225, 225, 0);
        }
        .set {
          position: absolute;
          font-family: "iconfont" !important;
          font-size: 20px;
          width: 54px;
          height: 40px;
          line-height: 40px;
          border-left: tovh(1) solid #ebebeb;
          border-top: tovh(1) solid #ebebeb;
          text-align: center;
          right: 0;
          z-index: 5;
          bottom: tovh(-24);
          color: #222222;
          & > span {
            display: inline-block;
            width: tovh(19);
            height: tovh(19);
            @media (-webkit-min-device-pixel-ratio: 1) {
              background-image: url("../../assets/icon/set.png");
            }
            @media (-webkit-min-device-pixel-ratio: 2) {
              background-image: url("../../assets/icon/set@2x.png");
            }
            @media (-webkit-min-device-pixel-ratio: 3) {
              background-image: url("../../assets/icon/set@3x.png");
            }
            background-size: tovh(19) tovh(19);
          }
        }
      }
      .kl-footer {
        width: 100%;
        height: 25px;
        background-color: #fff;
        font-size: 0;
        padding-left: 20px;
        border-top: tovh(1) solid #ebebeb;
        opacity: 1;
        position: relative;
        z-index: 3;
        & > span {
          display: inline-block;
          font-size: 13px;
          line-height: 25px;
          &.changeC {
            color: #ff7100;
          }
        }
        .one {
          padding-right: 15px;
        }
      }
    }
  }
  @media only screen and (device-width: 375px) and (device-height: 667px) {
    .detail {
      .kl-content-L {
        /*flex: 1;*/
        width: 613px !important;
        background-color: #f5f5f5;
        overflow: hidden;
      }
    }
  }
}
</style>



