<template>
<div class="changeItem">
  <div class="itemList" v-for='(item,index) in changeItemList' :key="index" @click="changeItem(item)">
  <div class="itemList-L" :class="{'chooseC':item.instrumentid===chooseName}">
    <span>{{item.name}}</span>
    <span>{{item.instrumentid}}</span>
  </div>
  <div class="itemList-R" >
    <span>{{item.NEWPRICE}}</span>
    <span :class="[item.diffPrice<0?'changeGre':'changeRed']">{{item.diffPriceRate}}</span>
  </div>
  </div>
</div>
</template>
<script>
export default {
  props:{
    changeItemList:{
      // type:Array
    },
    chooseName:{
      type:String
    }
  },
  created() {
  // console.log(this.changeItemList)
  },
  methods:{
    changeItem(data){
      this.$emit('changeItem',data)
    }
  }
}
</script>
<style lang="scss" scoped>
@import "../../style/theme2";
.changeItem {
  .itemList {
    height: tovh(50);
    width: 100%;
    background-color: #FFF;
    display: flex;
    padding: tovh(11) tovh(4) tovh(11) tovh(6);
    /*border-bottom: tovh(1) solid #d2d1d1;*/
    & span {
      display: block;
      white-space: nowrap;
    }
    .itemList-L {
      text-align: left;
      width: tovh(80);
      padding-left: tovh(16);
      &.chooseC {
        color: #1988FF;
      }
      &>span:nth-child(1){
        font-size: tovh(14);
        line-height: tovh(14);
        margin-bottom: tovh(5);
      }
      &>span:nth-child(2){
        font-size: tovh(13);
        line-height: tovh(10);
        color: rgba(0,0,0,.3);
      }
    }
    .itemList-R {
      flex: 1;
      text-align: right;
      padding-right: tovh(16);
      &>span:nth-child(1){
        font-family: 'iconfont'!important;
        font-size: tovh(14);
        line-height: tovh(14);
        margin-bottom: tovh(5);
      }
      &>span:nth-child(2){
        font-size: tovh(10);
        line-height: tovh(10);
        &.changeRed {
        color:#FD4F50;
      }
      &.changeGre {
        color:#07B76F;
      }
      }
    }
  }
}

</style>



