<template>
  <div class="remind">
    <div class="header">预警提醒
    <span @click="back">&#xe6a8;</span>
    </div>
    <div class="item">
      <span class="name">恒生指数</span>
      <span class="ename">HSI1912</span>
      <span class="price1">1234.56</span>
      <span class="price2">1.233%</span>
    </div>
    <div class="kong"></div>
    <div class="input"><span>价格上限</span><input type="text" v-model="high" class="high" placeholder="请输入目标价"></div>
    <div class="input"><span>价格下限</span><input type="text" v-model="low" class="low" placeholder="请输入目标价"></div>
    <div class="input"><span>日涨幅超</span><input type="text" v-model="up" class="up" placeholder="请输入涨幅值"><span class="per1">%</span></div>
    <div class="input"><span>日跌幅超</span><input type="text" v-model="down" class="down" placeholder="请输入跌幅值"><span  class="per2">%</span></div>
    <div class="set">设置</div>
  </div>
</template>

<script>
export default {
  data(){
    return {
      high:0,
      low:0,
      up:0,
      down:0,
    }
  },
  methods:{
    back(){
      this.$emit('hideRemind')
    }
  }
}
</script>

<style lang="scss" scoped>
  @import "../../style/theme2";
  .remind {
    width: tovw(375);
    height: 100vh;
    position: fixed;
    top:0;
    left: 0;
    background-color: #FFF;
    z-index: 9;
    padding-top: 28px;
    .header {
      width: 100%;
      height: tovw(50);
      background-color: #FFF;
      text-align: center;
      line-height: tovw(49);
      border-bottom: tovw(1) solid #EBEBEB;
      position: relative;
      font-weight: 600;
      font-size: tovw(20);
      color: #000;
      font-family: 'iconfont';
      &>span {
        position: absolute;
        height: tovw(50);
        line-height: tovw(50);
        left: tovw(20);
        top: tovw(0);
        font-weight: 500;
      }
    }
    .item {
      width: 100%;
      height: tovw(54);
      display: flex;
      &>span {
        display: inline-block;
        line-height: tovw(54);
      }
      .name {
      padding-left: tovw(10);
        font-size: tovw(14);
        width: tovw(80);
        color: #000;
      }
      .ename {
        padding-left: tovw(10);
        font-size: tovw(13);
        color: #808080;
        width: tovw(80);
      }
      .price1 {
        padding-left: tovw(20);
        font-size: tovw(14);
        width: tovw(100);
        color: #000;
      }
      .price2 {
        padding-left: tovw(10);
        padding-right: tovw(16);
        font-size: tovw(12);
        text-align: right;
        flex: 1;
      }
    }
    .kong {
      width: 100%;
      height: tovw(10);
      background-color: #F4F4F4;
    }
    .input {
      display: block;
      width: 100%;
      border-bottom: tovw(1) solid #EBEBEB;
      height: tovw(54);
      font-size: tovw(14);
      line-height: tovw(53);
      position: relative;
      &>span {
        height: tovw(53);
        font-size: tovw(14);
        line-height: tovw(53);
        color: #333333;
        padding-left: tovw(17);
      }
      .per1 {
        color: #E76237;
      }
      .per2 {
       color: #3EB87D;
      }
      &>span:nth-child(3) {
        position: absolute;
        right: tovw(17);
        top: 0;
      }
      input {
        position: absolute;
        top:0;
        right:0;
        border-width: 0 0 0 0;
        /*background-color: #71d3ff;*/
        text-align: right;
        margin-right: tovw(35);
      }
      input::-webkit-input-placeholder{
        color: #A8A8A8;
      }
    }
    .set {
      width: tovw(160);
      height: tovw(30);
      background-color: #2E8CFF;
      color: #FFF;
      border-radius: tovw(4);
      line-height: tovw(30);
      text-align: center;
      position: absolute;
      top: 60%;
      left: 50%;
      transform: translateX(-50%);
    }
  }
</style>



