    stoptip(e) {
      // console.log(e)
      this.X2.x = e.zrX;
      this.X2.y = e.zrY;
      // console.log((this.X2.y))
      let num = Math.abs((this.X2.y - this.X1.y) / (this.X2.x - this.X1.x));
      // console.log(num)
      if (num < 1) {
        //  e.preventDefault();                        //滑动小于45度阻止事件扩散 阻止划屏上下移动
        e.stopPropagation();
      }
      let widtH = document.getElementById("demo").offsetWidth;
      if (Number(e.zrX) / Number(widtH) < 0.5) {
        this.changeSide = false;
      } else if (Number(e.zrX) / Number(widtH) > 0.5) {
        this.changeSide = true;
      }
      // console.log(this.changeSide);
      if (this.stopClick === 1) {
        e.preventDefault(); //阻止事件扩散 产生上下滑动时带动划屏上下移动
        e.stopPropagation();
      }
      window.clearTimeout(this.timeOut); //阻止在单指移动时触发tooltip（tooltip未出现时）
    },
