    setXtime() {
      let Valuelist = [];
      let indexList = [];
      let LeftList = [];
      Valuelist.push(this.XtimeFen[0]);
      indexList.push(0);
      // console.log(this.XtimeFen,'this.XtimeFen')
      for (let i = 0, len = this.XtimeFen.length; i < len; i++) {
        if (i > 0) {
          let pre = Number(this.XtimeFen[i - 1].split(":")[0]);
          let next = Number(this.XtimeFen[i].split(":")[0]);
          let preT = Number(this.XtimeFen[i - 1].split(":")[1]);
          let nextT = Number(this.XtimeFen[i].split(":")[1]);
          if (pre > next) {
            // console.log(this.XtimeFen[i-1],this.XtimeFen[i]);
            // console.log(pre,next);
            let mix = this.XtimeFen[i - 1] + "/" + this.XtimeFen[i];
            if (
              this.XtimeFen[i - 1] === "23:59" &&
              this.XtimeFen[i] === "00:00"
            ) {
              continue;
            } else {
              Valuelist.push(mix);
              indexList.push(i);
            }
          } else if (next > pre) {
            if (next - pre > 1) {
              let mix = this.XtimeFen[i - 1] + "/" + this.XtimeFen[i];
              Valuelist.push(mix);
              indexList.push(i);
            }
          } else if (next === pre) {
            if (nextT - preT > 1) {
              let mix = this.XtimeFen[i - 1] + "/" + this.XtimeFen[i];
              Valuelist.push(mix);
              indexList.push(i);
            }
          }
        }
      }
      let TT = Number(this.XtimeFen[this.XtimeFen.length - 1].split(":")[0]);
      let tt =
        Number(this.XtimeFen[this.XtimeFen.length - 1].split(":")[1]) + 1;
      if (tt > 59) {
        tt = "00";
        TT = TT + 1;
        if (TT < 10) {
          TT = "0" + TT;
        }
      }
      Valuelist.push(TT + ":" + tt);
      indexList.push(this.XtimeFen.length - 1);
      let Wth = document.getElementById("demo").offsetWidth;
      for (let i = 0, len = indexList.length; i < len; i++) {
        LeftList.push((indexList[i] / this.XtimeFen.length) * Wth); //获取每一个节点的相对位置
      }
      LeftList[0] = 0; //第一个值靠左边
      this.xLiftList = LeftList;
      this.xValuelist = Valuelist;
      this.$nextTick(() => {
        let dom = document.getElementsByClassName("fenList");
        for (let i = 0, len = Valuelist.length; i < len; i++) {
          dom[i].style.left = LeftList[i] + "px";
          // console.log(LeftList[i]+'px')
        }
        let domW =
          document.getElementsByClassName("fenList")[0].offsetWidth + 20;
        for (let i = 0, len = LeftList.length; i < len; i++) {
          //设置不重叠
          if (
            i > 0 &&
            LeftList[i] + domW > LeftList[i + 1] &&
            i < LeftList.length - 1
          ) {
            if (i === LeftList.length - 2) {
              this.$set(this.xValuelist, i, "");
            } else {
              this.$set(this.xValuelist, i + 1, "");
            }
          }
        }
      });
    },
    getNum(num) {
      console.log('num :>> ', num);
      this.EMA12 = [];
      this.UP = [];
      this.DN = [];
      this.BBI = [];
      this.MA20 = [];
      this.volume = [];
      this.minute = [];
      if (
        this.chooseTime !== 111 &&
        this.maNum === num &&
        this.demo.length > 0 &&
        this.reloadLine === 0
      ) {
        return;
      }
      this.maNum = num;
      if (this.demo.length > 0) {
        this.i = 1;
      }
      if (this.chooseTime !== 1) {
        this.count = "&count=300";
      } else {
        let time = new Date();
        if (time.getDay() < 6 && time.getDay() !== 1 && time.getDay() !== 0) {
          // this.count='';
          let start = this.xTime[0];
          let y = time.getFullYear();
          let M = time.getMonth() + 1;
          if (M < 10) {
            M = "0" + M;
          }
          let D = time.getDate();
          if (D < 10) {
            D = "0" + D;
          }
          let StartMixTime = y + "/" + M + "/" + D + " " + start;
          let Stime = new Date(StartMixTime);
          Stime = Stime.getTime(); //将分时图第一个时间转为时间戳，日为当前日期
          if (time < Stime) {
            Stime = Stime - 86400000;
          }
          this.count = "&start=" + Stime;
        } else {
          //2013/03/08 17:20    var date = new Date(str); var time = date.getTime();
          let start = this.xTime[0];
          let end = this.xTime[this.xTime.length - 1];
          let y = time.getFullYear();
          let M = time.getMonth() + 1;
          if (M < 10) {
            M = "0" + M;
          }
          let D = time.getDate();
          if (D < 10) {
            D = "0" + D;
          }
          let StartMixTime = y + "/" + M + "/" + D + " " + start;
          let EndMixTime = y + "/" + M + "/" + D + " " + end;
          let Stime = new Date(StartMixTime);
          let Etime = new Date(EndMixTime);
          Stime = Stime.getTime();
          Etime = Etime.getTime();
          let ss = start.split(":")[0];
          let EE = end.split(":")[0];
          if (ss < EE) {
            //同一天
            if (time.getDay() === 6) {
              Stime = Stime - 86400000;
              Etime = Etime - 86400000;
            } else {
              Stime = Stime - 172800000;
              Etime = Etime - 172800000;
            }
          } else {
            //跨越到第二天
            if (time.getDay() === 6) {
              Stime = Stime - 86400000;
              this.count = "&start=" + Stime + "&end=" + Etime;
            } else if (time.getDay() === 1) {
              //周一特殊处理
              let time = new Date();
              let start = this.xTime[0];
              let y = time.getFullYear();
              let M = time.getMonth() + 1;
              if (M < 10) {
                M = "0" + M;
              }
              let D = time.getDate();
              if (D < 10) {
                D = "0" + D;
              }
              let StartMixTime = y + "/" + M + "/" + D + " " + start;
              let Stime = new Date(StartMixTime);
              Stime = Stime.getTime(); //将分时图第一个时间转为时间戳，日为当前日期
              if (time <= Stime) {
                Stime = Stime - 259200000;
                this.count = "&start=" + Stime;
              } else {
                this.count = "&start=" + Stime; //如果当前时间在周一开盘之后，那么默认请求开盘到现在
              }
            } else {
              Stime = Stime - 172800000;
              Etime = Etime - 86400000;
              this.count = "&start=" + Stime + "&end=" + Etime;
            }
          }
          // this.count='&start='+Stime+"&end="+Etime;
        }
      }
      // this.$axios.get('http://121.43.137.150:8189/bar?instrumentId='+this.instrumentId+'&period='+this.maNum+this.count).then((rsq)=>{
      // console.log(this.count,'this.count')
      console.log('this.$store.state.discUrl :>> ', this.$store.state.discUrl);
      console.log('this.instrumentId :>> ', this.instrumentId);
      console.log('this.maNum :>> ', this.maNum);
      console.log('this.count :>> ', this.count);
      let timestamp = (new Date()).getTime();
      this.$axios
        .get(
          "http://" +
            this.$store.state.discUrl +
            ":8189/bar?instrumentId=" +
            this.instrumentId +
            "&period=" +
            this.maNum +
            // "&end=" + timestamp
            "&count=999"
        )
        .then((rsq) => {
          console.log('rsq...... :>> ', rsq);
          if (this.demo.length > 0) {
            this.demo = [];
