            return;
          }
          for (let i = 0, len = data.length; i < len; i++) {
            let newTime = new Date(Number(data[i].time));
            let M = newTime.getMonth() + 1;
            let D = newTime.getDate();
            let h = newTime.getHours();
            if (h / 10 < 1) {
              h = "0" + h;
