    },
    setOption2() {
      let this_ = this;
      if (this.chooseTime === 1 && this.Whoshow === "1f") {
        this.getFenS();
        this.getFenMA();
        if (this.demo.length > 0) {
          // console.log(this.demo)
          let T = this.demo[this.demo.length - 1][4].split(" ")[1];
          let list5 = [];
          let list6 = [];
          let list7 = [];
          let list8 = [];
          for (let j = 0, len = this.xTime.length; j < len; j++) {
            if (T == this.xTime[j]) {
              break;
            }
            list5.push("-");
            list6.push(0);
            list7.push(["-", "-", "-", "-", "-", "-"]);
            list8.push("-");
          }
          for (let i = 0, len = this.demo.length; i < len; i++) {
            let mixTime = this.demo[i][4].split(" ")[1];
            for (let j = 0, len = this.xTime.length; j < len; j++) {
              if (mixTime == this.xTime[j]) {
                list5.splice(j, 1, this.minute[i]);
                list6.splice(j, 1, this.volume[i]);
                list7.splice(j, 1, this.demo[i]);
                list8.splice(j, 1, this.FenMA[i]);
                break;
              }
            }
          }
          console.log(list5);
          this.minute = list5;
          this.volume = list6;
          this.demo = list7;
          this.FenMA = list8;
        }
        // console.log(this.minute)
        this.List[0] = 0;
        this.List[1] = 24;
      } else if (this.chooseTime === 1 && this.Whoshow === "1w") {
        // this.List[1]=1;
      }
      let dataZoom = false;
      let showX = true;
      if (this.chooseTime === 1) {
        dataZoom = true;
        showX = false;
      }
      this.option2 = {
        xAxis: [
          {
            type: "category",
            data: this.xTime,
            scale: true,
            animation: false,
            boundaryGap: true,
            axisLine: {
              onZero: false,
              lineStyle: {
                color: "#F4F4F4",
              },
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: "rgba(228,228,228,0.3)",
              },
            },
            splitNumber: 20,
            min: "dataMin",
            // max: 'dataMax',
          },
          {
            type: "category",
            data: this.xTime,
            scale: true,
            gridIndex: 1,
            boundaryGap: true,
            axisLine: { onZero: true },
            axisTick: { show: false },
            splitLine: {
              show: true,
              lineStyle: {
                color: "rgba(228,228,228,0.3)",
              },
            },
            axisLabel: { show: showX, margin: 0 },
            axisPointer: {
              show: true,
              label: {
                padding: [1, 0, 3, 0],
              },
            },
            fontSize: 10,
            splitNumber: 20,
            min: "dataMin",
            // max: 'dataMax'
          },
