    getMA(P) {
      let num = P.param;
      let result = [];
      for (let i = 0, len = this.demo.length; i < len; i++) {
        if (i < num - 1) {
          result.push("-");
          continue;
        }
        let sum = 0;
        for (let j = 0; j < num; j++) {
          sum += Number(this.demo[i - j][1]);
        }
        let count = 0;
        if (this.digits > 4) {
          count = this.digits;
        } else {
          count = Number(this.digits) + 1;
        }
        result.push((sum / num).toFixed(count));
      }
      switch (P.type) {
        case "MA5":
          this.MA5 = result;
          break;
        case "MA10":
          this.MA10 = result;
          break;
        case "MA20":
          this.MA20 = result;
          break;
        case "MA40":
          this.MA40 = result;
          break;
      }
    },
    changeMA(p, change) {
      let num = p.param;
      let sum = 0;
      let i = this.demo.length - 1;
      if (this.demo.length < num) {
        return;
      }
      for (let j = 0; j < num; j++) {
        sum += Number(this.demo[i - j][1]);
      }
      let count = 0;
      if (this.digits > 4) {
        count = this.digits;
      } else {
        count = Number(this.digits) + 1;
      }
      let lastNum = (sum / num).toFixed(count);
      switch (p.type) {
        case "MA5":
          if (change === 1) {
            // console.log('MA加数据');
            this.$set(this.MA5, this.MA5.length, lastNum);
          } else {
            this.$set(this.MA5, this.MA5.length - 1, lastNum);
          }
          break;
        case "MA10":
          if (change === 1) {
            this.$set(this.MA10, this.MA10.length, lastNum);
          } else {
            this.$set(this.MA10, this.MA10.length - 1, lastNum);
          }
          break;
        case "MA20":
          if (change === 1) {
            this.$set(this.MA20, this.MA20.length, lastNum);
          } else {
            this.$set(this.MA20, this.MA20.length - 1, lastNum);
          }
          break;
        case "MA40":
          if (change === 1) {
            this.$set(this.MA40, this.MA40.length, lastNum);
          } else {
            this.$set(this.MA40, this.MA40.length - 1, lastNum);
          }
          break;
      }
    },
    getBOLL() {
      let count = 0;
      if (this.digits > 4) {
        count = this.digits;
      } else {
        count = 4;
      }
      let num = this.setParam.BOLL.Bparam1;
      for (let i = 0, len = this.demo.length; i < len; i++) {
        if (i < num) {
          this.$set(this.UP, this.UP.length, "-");
          this.$set(this.DN, this.DN.length, "-");
          this.$set(this.MA20, this.MA20.length, "-");
          continue;
        }
        let ma = 0;
        let mb = 0;
        let MB20 = 0;
        let MA20 = 0;
        for (let j = 0; j < num; j++) {
          ma += Number(this.demo[i - j][1]);
        }
        MA20 = (ma / num).toFixed(count);
        for (let j = 0; j < num; j++) {
          mb += Number(this.demo[i - 1 - j][1]); //计算MD的参数（上一日的MA）；
        }
        MB20 = mb / num;
        let mba = ma / num; //计算MD的参数（近20日的MA）；
        let mbb = 0; //累计（c-ma）的平方和;
        for (let j = 0; j < num; j++) {
          mbb += Math.pow(this.demo[i - j][1] - mba, 2);
        }
        let uu = mbb / num;
        let MD = Math.sqrt(uu);
        let UP = (MB20 + this.setParam.BOLL.Bparam2 * MD).toFixed(count);
        let DN = (MB20 - this.setParam.BOLL.Bparam2 * MD).toFixed(count);
        this.$set(this.UP, this.UP.length, UP);
        this.$set(this.DN, this.DN.length, DN);
        this.$set(this.MA20, this.MA20.length, MA20);
      }
      console.log(this.UP.length);
      console.log(this.DN.length);
      console.log(this.MA20.length);
    },
    changeBOLL(change) {
      let count = 0;
      if (this.digits > 4) {
        count = this.digits;
      } else {
        count = 4;
      }
      let num = this.setParam.BOLL.Bparam1;
      let ma = 0;
      let mb = 0;
      let MB20 = 0;
      let MA20 = 0;
      for (let j = 0; j < num; j++) {
        ma += Number(this.demo[this.demo.length - 1 - j][1]);
      }
      MA20 = (ma / num).toFixed(count);
      for (let j = 0; j < num; j++) {
        mb += Number(this.demo[this.demo.length - 2 - j][1]); //计算MD的参数（上一日的MA）；
      }
      MB20 = mb / num;
      let mba = ma / num; //计算MD的参数（近26日的MA）；
      let mbb = 0; //累计（c-ma）的平方和;
      for (let j = 0; j < num; j++) {
        mbb += Math.pow(this.demo[this.demo.length - 1 - j][1] - mba, 2);
      }
      let uu = mbb / num;
      let MD = Math.sqrt(uu);
      let UP = (MB20 + this.setParam.BOLL.Bparam2 * MD).toFixed(count);
      let DN = (MB20 - this.setParam.BOLL.Bparam2 * MD).toFixed(count);
      if (change === 1) {
        this.$set(this.UP, this.UP.length, UP);
        this.$set(this.DN, this.DN.length, DN);
        this.$set(this.MA20, this.MA20.length, MA20);
      } else if (change === 0) {
        this.$set(this.UP, this.UP.length - 1, UP);
        this.$set(this.DN, this.DN.length - 1, DN);
        this.$set(this.MA20, this.MA20.length - 1, MA20);
      }
    },
    getEMA(P) {
      let num = P.param;
      let count = 0;
      if (this.digits > 4) {
        count = this.digits;
      } else {
        count = 4;
      }
      let list1 = [];
      let list2 = [];
      let list3 = [];
      switch (P.type) {
        case "EMA5":
          this.$set(list1, 0, this.demo[0][1]);
          break;
        case "EMA12":
          this.$set(list2, 0, this.demo[0][1]);
          break;
        case "EMA20":
          this.$set(list3, 0, this.demo[0][1]);
          break;
      }
      for (let i = 1, len = this.demo.length; i < len; i++) {
        switch (P.type) {
          case "EMA5":
            let ema1 = (
              (this.demo[i][1] * 2 + list1[i - 1] * (num - 1)) /
              (num + 1)
            ).toFixed(count);
            list1.push(ema1);
            // this.$set(this.EMA5, this.EMA5.length, ema1);
            break;
          case "EMA12":
            let ema2 = (
              (this.demo[i][1] * 2 + list2[i - 1] * (num - 1)) /
              (num + 1)
            ).toFixed(count);
            list2.push(ema2);
            // this.$set(this.EMA12, this.EMA12.length, ema2);
            break;
          case "EMA20":
            let ema3 = (
              (this.demo[i][1] * 2 + list3[i - 1] * (num - 1)) /
              (num + 1)
            ).toFixed(count);
            list3.push(ema3);
            // this.$set(this.EMA20, this.EMA20.length, ema3);
            break;
        }
      }
      switch (P.type) {
        case "EMA5":
          this.EMA5 = list1;
          console.log(this.EMA5.length);
          break;
        case "EMA12":
          this.EMA12 = list2;
          console.log(this.EMA12.length);
          break;
        case "EMA20":
          this.EMA20 = list3;
          console.log(this.EMA20.length);
          break;
      }
    },
