    getBBI() {
      let count = 0;
      let num1 = this.setParam.BBI.param1;
      let num2 = this.setParam.BBI.param2;
      let num3 = this.setParam.BBI.param3;
      let num4 = this.setParam.BBI.param4;
      if (this.digits > 4) {
        count = this.digits;
      } else {
        count = 4;
      }
      for (let i = 0, len = this.demo.length; i < len; i++) {
        if (i < num4 - 1) {
          this.$set(this.BBI, this.BBI.length, "-");
          continue;
        }
        let sum3 = 0;
        let sum6 = 0;
        let sum12 = 0;
        let sum24 = 0;

        for (let j = 0; j < num1; j++) {
          sum3 += Number(this.demo[i - j][1]);
        }
        for (let j = 0; j < num2; j++) {
          sum6 += Number(this.demo[i - j][1]);
        }
        for (let j = 0; j < num3; j++) {
          sum12 += Number(this.demo[i - j][1]);
        }
        for (let j = 0; j < num4; j++) {
          sum24 += Number(this.demo[i - j][1]);
        }
        let BBI = (
          (sum3 / num1 + sum6 / num2 + sum12 / num3 + sum24 / num4) /
          4
        ).toFixed(count);
        this.$set(this.BBI, this.BBI.length, BBI);
      }
    },
    changeBBI(change) {
      let count = 0;
      if (this.digits > 4) {
        count = this.digits;
      } else {
        count = 4;
      }
      let sum3 = 0;
      let sum6 = 0;
      let sum12 = 0;
      let sum24 = 0;
      let num1 = this.setParam.BBI.param1;
      let num2 = this.setParam.BBI.param2;
      let num3 = this.setParam.BBI.param3;
      let num4 = this.setParam.BBI.param4;
      for (let j = 0; j < num1; j++) {
        sum3 += Number(this.demo[this.demo.length - 1 - j][1]);
      }
      for (let j = 0; j < num2; j++) {
        sum6 += Number(this.demo[this.demo.length - 1 - j][1]);
      }
      for (let j = 0; j < num3; j++) {
        sum12 += Number(this.demo[this.demo.length - 1 - j][1]);
      }
      for (let j = 0; j < num4; j++) {
        sum24 += Number(this.demo[this.demo.length - 1 - j][1]);
      }
      let BBI = (
        (sum3 / num1 + sum6 / num2 + sum12 / num3 + sum24 / num4) /
        4
      ).toFixed(count);
      if (change === 1) {
        this.$set(this.BBI, this.BBI.length, BBI);
      } else if (change === 0) {
        this.$set(this.BBI, this.BBI.length - 1, BBI);
      }
    },
    getDEA(n, data, field) {
      let count = 0;
      if (this.digits > 4) {
        count = this.digits;
      } else {
        count = 4;
      }
      var i, l, ema, a;
      a = 2 / (n + 1);
      if (field) {
        //二维数组
        ema = [data[0][field]];
        for (i = 1, l = data.length; i < l; i++) {
          ema.push((a * data[i][field] + (1 - a) * ema[i - 1]).toFixed(count));
        }
      } else {
        //普通一维数组
        ema = [data[0]];
        for (i = 1, l = data.length; i < l; i++) {
          ema.push((a * data[i] + (1 - a) * ema[i - 1]).toFixed(count));
        }
      }
      this.DEA = ema;
      // console.log(this.DEA);
      return this.DEA;
    },
