    },
    getDIF(short, long, data, field) {
      let count = 0;
      if (this.digits > 4) {
        count = this.digits;
      } else {
        count = 4;
      }
      let dif = [];
      let emaShort = this.getDEA(short, data, field);
      let emaLong = this.getDEA(long, data, field);
      // console.log(emaShort);
      // console.log(emaLong);
      for (let i = 0, l = data.length; i < l; i++) {
        dif.push((emaShort[i] - emaLong[i]).toFixed(count));
      }
      this.DIF = dif;
      return this.DIF;
