    calcLLV(pos, n, data, field) {
      var i, l, min;
      n--;
      min = data[pos][field];
      l = pos - n;
      l < 0 ? (l = 0) : 1;
      for (i = pos; i >= l; i--) {
        if (min > data[i][field]) {
          min = data[i][field];
        }
      }
      return min;
    },
    calcHHV(pos, n, data, field) {
      let i, l, max;
      n--;
      max = data[pos][field];
      l = pos - n;
      l < 0 ? (l = 0) : 1;
      for (i = pos; i >= l; i--) {
