    calcSMA(n, m, data) {
      let count = 0;
      if (this.digits > 4) {
        count = this.digits;
      } else {
        count = 4;
      }
      var i, l, sma;
      sma = [100];
      for (i = 1, l = data.length; i < l; i++) {
        sma.push(((data[i] * m + sma[i - 1] * (n - m)) / n).toFixed(count));
      }
      return sma;
    },
    changeSMA(n, m, data) {
      var i, l, sma;
      sma = [100];
      sma =
        (data[data.length - 1] * m + sma[data.length - 1 - 1] * (n - m)) / n;
      // console.log(sma)
      return sma;
    },
    calcRSV(n, data) {
      var i, l, rsv, low, tempValue;
      rsv = [];
      for (i = 0, l = data.length; i < l; i++) {
        low = this.calcLLV(i, n, data, 2);
        tempValue =
          (100 * (data[i][1] - low)) / (this.calcHHV(i, n, data, 3) - low);
        if (isNaN(tempValue)) {
          if (i === 0) {
            tempValue = 100;
          } else {
            tempValue = rsv[i - 1];
          }
        }
        rsv.push(tempValue);
      }
      return rsv;
    },
    changeRSV(n, data) {
      var i, l, rsv, low, tempValue;
      rsv = [];
      low = this.calcLLV(data.length - 1, n, data, 2);
      tempValue =
        (100 * (data[data.length - 1][1] - low)) /
        (this.calcHHV(data.length - 1, n, data, 3) - low);
      if (isNaN(tempValue)) {
        if (i === 0) {
          tempValue = 100;
        } else {
          tempValue = rsv[data.length - 1];
        }
      }
      rsv = tempValue;
      // console.log(rsv,'rsv')
      return rsv;
    },
    changeKDJ(n, m1, m2, data) {
      var i, l, rsv, k, d, j;
      rsv = this.changeRSV(n, data);
      k = this.changeSMA(m1, 1, rsv);
      d = this.changeSMA(m2, 1, k);
      j = 3 * k[0] - 2 * d[0];
      this.D = d;

      this.J = j;
      this.K = k;
      return {
        k: k,
        d: d,
        j: j,
      };
    },
    calcKDJ(n, m1, m2, data) {
      let count = 0;
      if (this.digits > 4) {
        count = this.digits;
      } else {
        count = 4;
      }
      var i, l, rsv, k, d, j;
      rsv = this.calcRSV(n, data);
      k = this.calcSMA(m1, 1, rsv);
      d = this.calcSMA(m2, 1, k);
      j = [];
      for (i = 0, l = k.length; i < l; i++) {
        j.push((3 * k[i] - 2 * d[i]).toFixed(count));
      }
      this.D = d;
      this.J = j;
      this.K = k;
      return {
        k: k,
        d: d,
        j: j,
      };
