    getRSI(num) {
      let result = [];
      for (let i = 0, len = this.demo.length; i < len; i++) {
        if (i < num - 1) {
          result.push("-");
          continue;
        }
        let sumUp = 0;
        let sumDw = 0;
        let rsi = 0;
        for (let j = 0; j < num; j++) {
          if (this.demo[i - j] && this.demo[i - j - 1]) {
            if (this.demo[i - j][1] >= this.demo[i - j - 1][1]) {
              //涨
              sumUp =
                sumUp +
                Number(this.demo[i - j][1]) -
                Number(this.demo[i - j - 1][1]);
            } else if (this.demo[i - j][1] < this.demo[i - j - 1][1]) {
              sumDw =
                sumDw +
                Number(this.demo[i - j][1]) -
                Number(this.demo[i - j - 1][1]); //跌
            }
            let rs = 0;
            if (sumUp == 0 && sumDw !== 0) {
              rs = 0;
            }
            if (sumUp !== 0 && sumDw == 0) {
              rs = 1;
            }
            if (sumUp !== 0 && sumDw !== 0) {
              let RS = Number(sumUp) / num / (Math.abs(Number(sumDw)) / num);
              rs = RS / (1 + RS);
            }
            rsi = 100 * rs; //100*RS(1+RS)
          }
        }
        result.push(Number(rsi).toFixed(3));
      }
      switch (num) {
        case 6:
          this.RSI6 = result;
          break;
        case 12:
          this.RSI12 = result;
          break;
        case 24:
          this.RSI24 = result;
          break;
      }
    },
    getDDI(N, N1, M, M1) {
      //N=13 N1=30 M=10 M1=5    M为权重
      let DMZ = [];
      let DMF = [];
      let DIZ = [];
      let DIF = [];
      let DDI = [];
      let ADDI = [];
      let AD = [];
      for (let i = 0, len = this.demo.length; i < len; i++) {
        if (i === 0) {
          DMZ.push("-");
          DMF.push("-");
        } else {
          let bool1 = this.demo[i][3] + this.demo[i][2];
          let bool2 = this.demo[i - 1][3] + this.demo[i - 1][2];
          if (bool1 >= bool2) {
            DMF.push(0);
            if (
              Math.abs(this.demo[i][3] - this.demo[i - 1][2]) >=
              Math.abs(this.demo[i][2] - this.demo[i - 1][2])
            ) {
              DMZ.push(Math.abs(this.demo[i][3] - this.demo[i - 1][2]));
            } else {
              DMZ.push(Math.abs(this.demo[i][2] - this.demo[i - 1][2]));
            }
          } else if (bool1 <= bool2) {
            DMZ.push(0);
            if (
              Math.abs(this.demo[i][3] - this.demo[i - 1][2]) >=
              Math.abs(this.demo[i][2] - this.demo[i - 1][2])
            ) {
              DMF.push(Math.abs(this.demo[i][3] - this.demo[i - 1][2]));
            } else {
              DMF.push(Math.abs(this.demo[i][2] - this.demo[i - 1][2]));
            }
          }
        }
      }
      // debugger
      //计算DDI
      for (let j = 0, len = DMZ.length; j < len; j++) {
        if (j < N) {
          //第一个DMZ不存在 跳过 所以<N
          DIZ.push("-");
          DIF.push("-");
          DDI.push("-");
        } else {
          let sum1 = 0;
          let sum2 = 0;
          for (let i = 0; i < N; i++) {
            sum1 += Number(DMZ[j - i]);
            sum2 += Number(DMF[j - i]);
          }
          let diz = sum1 / (sum1 + sum2);
          let diF = sum2 / (sum1 + sum2);
          DIZ.push(diz);
          DIF.push(diF);
          DDI.push((diz - diF).toFixed(2));
        }
      }
      //ADDI:SMA(DDI,N1,M);
      for (let k = 0, len = DDI.length; k < len; k++) {
        if (k < N) {
          ADDI.push("-");
        } else {
          let addi = 0;
          let sum = 0;
          // debugger
          if (k < N + N1) {
            for (let i = 0, len = k - N + 1; i < len; i++) {
              sum += Number(DDI[k - i]);
            }
            addi = sum / (k - N + 1);
            console.log(sum, k - N + 1);
          } else {
            addi = (Number(DDI[k]) * M + ADDI[k - 1] * (N1 - M)) / N1;
          }
          ADDI.push(addi.toFixed(2));
        }
      }
      //计算AD
      for (let i = 0, len = DDI.length; i < len; i++) {
        if (i < N - 1 + 5) {
          AD.push("-");
          continue;
        }
        let sum = 0;
        for (let j = 0; j < M1; j++) {
          sum += Number(DDI[i - j]);
        }
        AD.push((sum / M1).toFixed(2));
      }
      this.DDI = DDI;
      this.ADDI = ADDI;
      this.AD = AD;
      console.log(this.ADDI.length);
      console.log(this.DDI);
      console.log(this.AD.length);
      console.log(this.demo.length);
    },
    getCDP() {
      let count = 0;
      if (this.digits > 4) {
        count = this.digits;
      } else {
        count = 4;
      }
      let AH = [];
      let NH = [];
      let NL = [];
      let AL = [];
      if (this.demo.length < 1) {
        return;
      }
      for (let i = 0; i < this.demo.length; i++) {
        if (i === 0) {
          AH.push("-");
          NH.push("-");
          NL.push("-");
          AL.push("-");
        } else {
          let CDP =
            (this.demo[i - 1][3] +
              this.demo[i - 1][2] +
              this.demo[i - 1][1] * 2) /
            4;
          let ah = CDP + (this.demo[i - 1][3] - this.demo[i - 1][2]);
          let nh = CDP * 2 - this.demo[i - 1][2];
          let al = CDP - (this.demo[i - 1][3] - this.demo[i - 1][2]);
          let nl = CDP * 2 - this.demo[i - 1][3];
          AH.push(Number(ah).toFixed(count));
          NH.push(Number(nh).toFixed(count));
          AL.push(Number(al).toFixed(count));
          NL.push(Number(nl).toFixed(count));
        }
      }
      this.AH = AH;
      this.NH = NH;
      this.NL = NL;
      this.AL = AL;
      // alert(this.AH[this.AH.length-1])
      // console.log(this.NH)
      // console.log(this.NL)
      // console.log(this.AL)
    },
