    getHMA(P) {
      let num = P.param;
      let result = [];
      for (let i = 0, len = this.demo.length; i < len; i++) {
        if (i < num - 1) {
          result.push("-");
          continue;
        }
        let sum = 0;
        for (let j = 0; j < num; j++) {
          sum += Number(this.demo[i - j][3]);
        }
        let count = 0;
        if (this.digits > 4) {
          count = this.digits;
        } else {
          count = 4;
        }
        result.push((sum / num).toFixed(count));
      }
      switch (P.type) {
        case "HMA6":
          this.HMA6 = result;
          break;
        case "HMA12":
          this.HMA12 = result;
          break;
        case "HMA30":
          this.HMA30 = result;
          break;
      }
    },
    changeHMA(p, change) {
      let num = p.param;
      let sum = 0;
      let i = this.demo.length - 1;
      if (this.demo.length < num) {
        return;
      }
      for (let j = 0; j < num; j++) {
        sum += Number(this.demo[i - j][3]);
      }
      let count = 0;
      if (this.digits > 4) {
        count = this.digits;
      } else {
        count = 4;
      }
      let lastNum = (sum / num).toFixed(count);
      switch (p.type) {
        case "HMA6":
          if (change === 1) {
            // console.log('MA加数据');
            this.$set(this.HMA6, this.HMA6.length, lastNum);
          } else {
            this.$set(this.HMA6, this.HMA6.length - 1, lastNum);
          }
          break;
        case "HMA12":
          if (change === 1) {
            this.$set(this.HMA12, this.HMA12.length, lastNum);
          } else {
            this.$set(this.HMA12, this.HMA12.length - 1, lastNum);
          }
          break;
        case "HMA30":
          if (change === 1) {
            this.$set(this.HMA30, this.HMA30.length, lastNum);
          } else {
            this.$set(this.HMA30, this.HMA30.length - 1, lastNum);
          }
          break;
      }
    },
    calcJLHB(N, M) {
      let data = this.demo;
      let i, l, arr, idx, VAR1, VAR2, B, JLHB;
      VAR1 = [];
      VAR2 = [];
      idx = [];
      arr = [];
      JLHB = {};
      for (i = 0, l = data.length; i < l; i++) {
        let low = this.calcLLV(i, 60, data, 2);
        let high = this.calcHHV(i, 60, data, 3);
        VAR1.push(((data[i][1] - low) / (high - low)) * 80);
      }
      B = this.calcSMA(N, 1, VAR1);
      VAR2 = this.calcSMA(M, 1, B);
      for (let i = 0; i < B.length; i++) {
        if (B[i] > VAR2[i] && B[i] < 40) {
          idx.push(i);
        }
        arr.push(0);
      }
      let idxArr = this.arrange(idx);
      for (let i = 0; i < idxArr.length; i++) {
        arr[idxArr[i]] = 50;
      }
      JLHB.B = B;
      JLHB.VAR2 = VAR2;
      JLHB.JLHBArr = arr;
      this.JLHB = JLHB;
    },
