    arrange(source) {
      let t;
      let ta;
      let r = [];
      source.forEach(function (v) {
        // console.log(t, v);   // 跟踪调试用
        if (t === v) {
          ta.push(t);
          t++;
          return;
        }
        ta = [v];
        t = v + 1;
        r.push(ta);
      });
      let oneArr = [];
      for (let i = 0; i < r.length; i++) {
        oneArr.push(r[i][0]);
      }
      return oneArr;
    },
    updateKline(newVal) {
      let this_ = this;
