    reloadLine(newval) {
      if (newval > 0) {
        this.setParam = JSON.parse(localStorage.setP);
        this.$forceUpdate();
        this.EMA5 = [];
        this.EMA20 = [];
        this.EMA12 = [];
        this.wsOk = false;
        this.idNum = this.instrumentId;
        this.init();
        this.myCharts.showLoading("default", {
          text: "加载中...",
          color: "#FF7100",
          maskColor: "#FFF",
        });
        if (this.chooseTime === 111 || this.chooseTime === 1) {
          this.getNum(1);
        } else {
          this.getNum(this.chooseTime);
        }
        this.stopClick = 1;
        this.tipend();
        let this_ = this;
        // this.myCharts.on('dataZoom',function (params) {
        //  if(this_.stopClick === 1){               //解决两指缩放触发tooltip问题
        //     window.clearTimeout(this.timeOut);
        //     this_.tipend();
        //   }
        // });
        this.$emit("changeReloadN");
        this.setParam = JSON.parse(localStorage.setP);
      }
    },
