    AllTick(newVal) {
      let path = this.$route.path;
      path = path.split("/");
      path = path[path.length - 1];
      if (this.change === 1 || this.demo.length === 0 || path == "setline") {
        return;
      }
      this.updateKline(newVal);
      return;
      let type = 0;
      for (let i = 0, len = newVal.length; i < len; i++) {
        if (this.instrumentId == newVal[i].instrumentid) {
          let arr = newVal[i];
          if (arr["instrumentid"] === this.idNum) {
            this.Tick = arr;
            if (arr["time"] - this.time < this.maNum * 60000) {
              console.log(
                arr["time"],
                this.time,
                arr["time"] - this.time,
                this.maNum * 60000,
                99999999
              );
              if (arr["price"] > this.newArr[3]) {
                // console.log("增加了");
                this.$set(this.newArr, 3, arr["price"]); //设置最高价
                this.$set(this.demo, this.demo.length - 1, this.newArr);
              } else if (arr["price"] < this.newArr[2]) {
                // console.log("减小了");
                this.$set(this.newArr, 2, arr["price"]); //设置最低价
                this.$set(this.demo, this.demo.length - 1, this.newArr);
              }
              this.$set(this.newArr, 1, arr["price"]); //设置收盘价
              this.currentVol += Number(arr["tickvol"]);
              this.$set(this.volume, this.volume.length - 1, this.currentVol);
              if (this.stopClick !== 1) {
                this.tipListV.Volume = this.currentVol;
              }
            } else {
              //在加数据之前渲染一次图表，更新最后一条bur线数据和指标数据
              switch (this.type) {
                case "MA":
                  this.changeMA(this.setParam.MA5, 0);
                  this.changeMA(this.setParam.MA10, 0);
                  this.changeMA(this.setParam.MA20, 0);
                  this.changeMA(this.setParam.MA40, 0);
                  this.$set(
                    this.demo[this.demo.length - 1],
                    5,
                    this.MA5[this.MA5.length - 1]
                  );
                  this.$set(
                    this.demo[this.demo.length - 1],
                    6,
                    this.MA10[this.MA10.length - 1]
                  );
                  this.$set(
                    this.demo[this.demo.length - 1],
                    7,
                    this.MA20[this.MA20.length - 1]
                  );
                  this.$set(
                    this.demo[this.demo.length - 1],
                    8,
                    this.MA40[this.MA40.length - 1]
                  );
                  if (this.stopClick === 1) {
                    break;
                  }
                  this.tipListV.MA5 = this.MA5[this.MA5.length - 1];
                  this.tipListV.MA10 = this.MA10[this.MA10.length - 1];
                  this.tipListV.MA20 = this.MA20[this.MA20.length - 1];
                  this.tipListV.MA40 = this.MA40[this.MA40.length - 1];
                  break;
                case "BOLL":
                  this.changeBOLL(0);
                  this.$set(
                    this.demo[this.demo.length - 1],
                    5,
                    this.UP[this.UP.length - 1]
                  );
                  this.$set(
                    this.demo[this.demo.length - 1],
                    6,
                    this.MA20[this.MA20.length - 1]
                  );
                  this.$set(
                    this.demo[this.demo.length - 1],
                    7,
                    this.DN[this.DN.length - 1]
                  );
                  if (this.stopClick === 1) {
                    break;
                  }
                  this.tipListV.UP = this.UP[this.UP.length - 1];
                  this.tipListV.MID = this.MA20[this.MA20.length - 1];
                  this.tipListV.DN = this.DN[this.DN.length - 1];
                  break;
                case "EMA":
                  this.changeEMA(this.setParam.EMA12, 0);
                  this.changeEMA(this.setParam.EMA5, 0);
                  this.changeEMA(this.setParam.EMA20, 0);
                  this.$set(
                    this.demo[this.demo.length - 1],
                    5,
                    this.EMA5[this.EMA5.length - 1]
                  );
                  this.$set(
